@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
.btn{background:#58595b;color:#b1b4ba;font-family:"Orbitron",monospace,sans-serif;font-size:18px;outline:none}.btn:hover,.btn:focus{border:1px solid #b1b4ba}#calculator-container{width:50%;height:60%;max-width:400px;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(6, 1fr);grid-template-areas:"display display display display" "clear clear equal equal" "numbers numbers numbers operations" "numbers numbers numbers operations" "numbers numbers numbers operations" "zero zero decimal operations";border:3px solid #f90}.grid-display{grid-area:display}.grid-clear{grid-area:clear}.grid-equal{grid-area:equal}.grid-numbers{grid-area:numbers}.grid-zero{grid-area:zero}.grid-decimal{grid-area:decimal}.grid-operations{grid-area:operations}#display-container,#clear,#equals{color:white}#display-container{background:#58595b;text-align:right;word-wrap:break-word;display:flex;flex-direction:column;justify-content:flex-end}#formula-display,#display{margin:0.25em}#formula-display{color:#f90;font-size:14px}#numbers{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(3, 1fr)}#operations{display:flex;flex-direction:column}.operation-element{background:#414042;height:25%}#clear{background:#ac3839}#equals{background:#004566}@media (max-width: 750px) and (orientation: landscape){#calculator-container{height:90%}}@media (max-width: 570px){#calculator-container{width:60%}}@media (max-width: 470px){#calculator-container{width:70%}}@media (max-width: 370px){#calculator-container{width:80%}}@media (max-width: 270px){#calculator-container{width:90%}}*{margin:0}#main-window{width:100%;height:100vh;display:flex;justify-content:center;align-items:center;font-family:"Orbitron",monospace,sans-serif;background:#b1b4ba;font-size:20px}

